import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { CSVLink } from 'react-csv';
import { Bar } from 'react-chartjs-2';
import 'chart.js/auto';

const DailyLogTable = () => {
  const [dailyLogs, setDailyLogs] = useState([]);
  const [smartListMap, setSmartListMap] = useState({});
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const savedDailyLogs = JSON.parse(localStorage.getItem('dailyLogs'));
    const savedSmartListMap = JSON.parse(localStorage.getItem('smartListMap'));
    if (savedDailyLogs && savedSmartListMap) {
      setDailyLogs(savedDailyLogs);
      setSmartListMap(savedSmartListMap);
      setLoading(false);
    } else {
      fetchSelectionsAndLogs();
    }
  }, []);

  const fetchSelectionsAndLogs = async () => {
    console.log("Fetching selections and logs");
    const startTime = Date.now();
    try {
      const savedSelections = JSON.parse(localStorage.getItem('selections'));
      if (savedSelections) {
        const { agents, smartLists } = savedSelections;
        console.log('Saved selections:', savedSelections);

        const response = await axios.post('http://localhost:3000/api/selected-counts', {
          agentIds: agents.map(agent => agent.id),
          smartListIds: smartLists.map(smartList => smartList.id),
        });

        console.log("Response received:", response.data);
        if (response.data) {
          const mappedLogs = Object.entries(response.data.counts).map(([agentId, smartListCounts]) => ({
            agentId,
            agentName: response.data.agentMap[agentId],
            smartListCounts,
            total: Object.values(smartListCounts).reduce((acc, count) => acc + count, 0),
            date: response.data.date,
          }));

          setDailyLogs(mappedLogs);
          setSmartListMap(response.data.smartListMap || {});

          // Save to local storage
          localStorage.setItem('dailyLogs', JSON.stringify(mappedLogs));
          localStorage.setItem('smartListMap', JSON.stringify(response.data.smartListMap || {}));
        } else {
          console.error('No data returned from API.');
        }
      } else {
        console.error('No saved selections found.');
      }
    } catch (error) {
      console.error('Error fetching daily logs:', error);
    } finally {
      setLoading(false);
      const endTime = Date.now();
      console.log(`Fetch and render took ${endTime - startTime} ms`);
    }
  };

  const handleClearLogs = async () => {
    if (window.confirm('Are you sure you want to clear the daily logs?')) {
      try {
        await axios.delete('http://localhost:3000/api/daily-logs');
        setDailyLogs([]);
        localStorage.removeItem('dailyLogs');
        localStorage.removeItem('smartListMap');
      } catch (error) {
        console.error('Error clearing daily logs:', error);
      }
    }
  };

  const handleBackToSelection = () => {
    navigate('/selection');
  };

  const data = {
    labels: dailyLogs.map(log => log.agentName),
    datasets: [
      {
        label: 'Total Leads',
        data: dailyLogs.map(log => log.total),
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  };

  return (
    <div className="p-6">
      <h1 className="text-4xl font-bold mb-6 text-center">Daily Log</h1>
      {loading ? (
        <div className="text-center">Loading...</div>
      ) : (
        <>
          <div className="flex justify-between mb-6">
            <button
              onClick={handleBackToSelection}
              className="px-6 py-3 bg-blue-500 text-white rounded hover:bg-blue-600 transition duration-300"
            >
              Back to Selection
            </button>
            <button
              onClick={handleClearLogs}
              className="px-6 py-3 bg-red-500 text-white rounded hover:bg-red-600 transition duration-300"
            >
              Clear Daily Logs
            </button>
            <CSVLink
              data={dailyLogs.map(log => ({
                date: new Date(log.date).toLocaleDateString(),
                agentName: log.agentName,
                ...log.smartListCounts,
                total: log.total,
              }))}
              filename="daily_logs.csv"
              className="px-6 py-3 bg-green-500 text-white rounded hover:bg-green-600 transition duration-300"
            >
              Export CSV
            </CSVLink>
          </div>
          <div className="overflow-x-auto mb-6">
            <Bar data={data} options={{ maintainAspectRatio: false }} height={400} />
          </div>
          {dailyLogs.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="min-w-full bg-white rounded-lg shadow-lg table-auto">
                <thead>
                  <tr>
                    <th className="py-3 px-6 bg-gray-800 text-white text-center text-xl">Date</th>
                    <th className="py-3 px-6 bg-gray-800 text-white text-center text-xl">Agent's Name</th>
                    {Object.keys(dailyLogs[0].smartListCounts || {}).map(smartListId => (
                      <th key={smartListId} className="py-3 px-6 bg-gray-800 text-white text-center text-xl">{smartListMap[smartListId]}</th>
                    ))}
                    <th className="py-3 px-6 bg-gray-800 text-white text-center text-xl">Total</th>
                  </tr>
                </thead>
                <tbody>
                  {dailyLogs.map(log => (
                    <tr key={log.agentId} className="border-b border-gray-200 hover:bg-gray-100 transition duration-300">
                      <td className="py-4 px-6 text-center">{new Date(log.date).toLocaleDateString()}</td>
                      <td className="py-4 px-6 text-center">{log.agentName}</td>
                      {Object.entries(log.smartListCounts || {}).map(([smartListId, count]) => (
                        <td key={smartListId} className="py-4 px-6 text-center">{count}</td>
                      ))}
                      <td className="py-4 px-6 text-center">{log.total}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="text-center text-gray-600">No logs available.</div>
          )}
        </>
      )}
    </div>
  );
};

export default DailyLogTable;
