import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import 'tailwindcss/tailwind.css';
import { motion } from 'framer-motion';

const SelectionComponent = () => {
  const [agents, setAgents] = useState([]);
  const [smartLists, setSmartLists] = useState([]);
  const [selectedAgents, setSelectedAgents] = useState([]);
  const [selectedSmartLists, setSelectedSmartLists] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchAgents = async () => {
      try {
        const response = await axios.get('http://localhost:3000/api/users');
        const sortedAgents = Object.entries(response.data).sort(([, a], [, b]) => a.localeCompare(b));
        setAgents(sortedAgents.map(([id, name]) => ({ id, name })));
      } catch (error) {
        console.error('Error fetching agents:', error);
      }
    };

    const fetchSmartLists = async () => {
      try {
        const response = await axios.get('http://localhost:3000/api/smartlists');
        const sortedSmartLists = Object.entries(response.data).sort(([, a], [, b]) => a.localeCompare(b));
        setSmartLists(sortedSmartLists.map(([id, name]) => ({ id, name })));
      } catch (error) {
        console.error('Error fetching smart lists:', error);
      }
    };

    const fetchData = async () => {
      await fetchAgents();
      await fetchSmartLists();
      setLoading(false);
      const savedSelections = JSON.parse(localStorage.getItem('selections'));
      if (savedSelections) {
        setSelectedAgents(savedSelections.agents || []);
        setSelectedSmartLists(savedSelections.smartLists || []);
      }
    };

    fetchData();
  }, []);

  const handleAgentSelection = (agent) => {
    setSelectedAgents(prevState => {
      const alreadySelected = prevState.find(item => item.id === agent.id);
      if (alreadySelected) {
        return prevState.filter(item => item.id !== agent.id);
      } else {
        return [...prevState, agent];
      }
    });
  };

  const handleSmartListSelection = (smartList) => {
    setSelectedSmartLists(prevState => {
      const alreadySelected = prevState.find(item => item.id === smartList.id);
      if (alreadySelected) {
        return prevState.filter(item => item.id !== smartList.id);
      } else {
        return [...prevState, smartList];
      }
    });
  };

  const handleSaveSelections = () => {
    const selections = {
      agents: selectedAgents,
      smartLists: selectedSmartLists,
    };
    localStorage.setItem('selections', JSON.stringify(selections));
    alert('Selections saved successfully!');
  };

  const handleClearSelections = () => {
    setSelectedAgents([]);
    setSelectedSmartLists([]);
    localStorage.removeItem('selections');
  };

  return (
    <div className="p-8 bg-gray-100 min-h-screen">
      <div className="max-w-4xl mx-auto bg-white shadow-lg rounded-lg overflow-hidden">
        <div className="p-6">
          <h1 className="text-3xl font-bold mb-6 text-center text-gray-900">Select Agents and Smart Lists</h1>
          {loading ? (
            <div className="text-center text-gray-600">Loading...</div>
          ) : (
            <>
              <div className="mb-6">
                <h2 className="text-xl font-semibold mb-2 text-gray-800">Agents</h2>
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4 overflow-auto max-h-64">
                  {agents.map(agent => (
                    <motion.button
                      key={agent.id}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      onClick={() => handleAgentSelection(agent)}
                      className={`px-4 py-2 rounded-lg shadow ${
                        selectedAgents.find(item => item.id === agent.id) ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-800'
                      }`}
                    >
                      {agent.name}
                    </motion.button>
                  ))}
                </div>
              </div>
              <div className="mb-6">
                <h2 className="text-xl font-semibold mb-2 text-gray-800">Smart Lists</h2>
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4 overflow-auto max-h-64">
                  {smartLists.map(smartList => (
                    <motion.button
                      key={smartList.id}
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      onClick={() => handleSmartListSelection(smartList)}
                      className={`px-4 py-2 rounded-lg shadow ${
                        selectedSmartLists.find(item => item.id === smartList.id) ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-800'
                      }`}
                    >
                      {smartList.name}
                    </motion.button>
                  ))}
                </div>
              </div>
              <div className="flex justify-around mb-6">
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handleSaveSelections}
                  className="px-4 py-2 bg-yellow-600 text-white rounded-lg shadow hover:bg-yellow-700 transition duration-300"
                >
                  Save Selections
                </motion.button>
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={handleClearSelections}
                  className="px-4 py-2 bg-gray-600 text-white rounded-lg shadow hover:bg-gray-700 transition duration-300"
                >
                  Clear Selections
                </motion.button>
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => navigate('/daily-log')}
                  className="px-4 py-2 bg-green-600 text-white rounded-lg shadow hover:bg-green-700 transition duration-300"
                >
                  Go to Daily Log
                </motion.button>
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => navigate('/scoreboard')}
                  className="px-4 py-2 bg-red-600 text-white rounded-lg shadow hover:bg-red-700 transition duration-300"
                >
                  Go to Scoreboard
                </motion.button>
              </div>
              <div className="mt-6">
                <h2 className="text-xl font-semibold mb-4 text-gray-800">Selected Agents and Smart Lists</h2>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <h3 className="text-lg font-bold text-gray-800">Agents</h3>
                    {selectedAgents.map(agent => (
                      <div key={agent.id} className="p-2 bg-blue-100 rounded-lg shadow mb-2">
                        {agent.name}
                      </div>
                    ))}
                  </div>
                  <div>
                    <h3 className="text-lg font-bold text-gray-800">Smart Lists</h3>
                    {selectedSmartLists.map(smartList => (
                      <div key={smartList.id} className="p-2 bg-blue-100 rounded-lg shadow mb-2">
                        {smartList.name}
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default SelectionComponent;
