import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import 'chart.js/auto';

const AnalyticsDashboard = () => {
  const [metrics, setMetrics] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchMetrics = async () => {
      try {
        const response = await axios.get('http://localhost:3000/api/metrics');
        setMetrics(response.data.metrics);
      } catch (error) {
        console.error('Error fetching metrics:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchMetrics();
  }, []);

  const data = {
    labels: metrics.map(metric => new Date(metric.date).toLocaleDateString()),
    datasets: [
      {
        label: 'Calls Made',
        data: metrics.map(metric => metric.callsMade),
        borderColor: 'rgba(75, 192, 192, 1)',
        fill: false,
      },
      {
        label: 'Deals Closed',
        data: metrics.map(metric => metric.dealsClosed),
        borderColor: 'rgba(192, 75, 192, 1)',
        fill: false,
      },
      {
        label: 'Conversion Rate',
        data: metrics.map(metric => metric.conversionRate),
        borderColor: 'rgba(192, 192, 75, 1)',
        fill: false,
      },
    ],
  };

  return (
    <div className="p-6">
      <h1 className="text-4xl font-bold mb-6 text-center">Analytics Dashboard</h1>
      {loading ? (
        <div className="text-center">Loading...</div>
      ) : (
        <div className="overflow-x-auto mb-6">
          <Line data={data} options={{ maintainAspectRatio: false }} height={400} />
        </div>
      )}
    </div>
  );
};

export default AnalyticsDashboard;
